<script>
import Message from '@/components/Message';
export default {
    name: 'Dashboard',
    data() {
        return {
			notON:false,
			ON:false,
			onAC:false,
            profile_picture: require('@/assets/can.jpg'),
            //ACTIVITIES
            menu: false,
            toggleAudio: true,
            toggleScreenShare: true,
            displayWidth: 500,
            displayHeight: 500,
            //ALERT
            alertType: undefined,
            alertShow: false,
            alertMessage: '',

            searchPeers: '',
            searchError: '',
            searchResult: false,
            searchResults: [],
            totalSearchPages: 0,
            connectionHeaders: {
                'Access-Control-Allow-Origin': '*',
                'Authorization': 'Bearer ' + this.$store.getters.getApiToken
            },
            //ADD PEER
            progress: false,
            //STREAMS
            localScreenStream: new MediaStream(),
            remoteScreenStream: new MediaStream(),
            constraint: {
                audio: true,
                video: true
            },
            remotePeerId: null,
            mediaConfig: {
                'video': true,
                'audio': false
            },
            peer: null,
            networkConfig: {
                'iceServers': [{
                    'urls': 'stun:stun.l.google.com:19302'
                }, {
                    'urls': 'turn:numb.viagenie.ca',
                    'credential': 'ADONIS123',
                    'username': 'sonobktech@gmail.com'
                }]
            },
            caller: null,
            callerSocketId: null,
            videoCallOverlay:false,
        }
    },
    watch: {
        toggleAudio() {
            if (!this.toggleAudio) {
                //TURN IT OFF
                this.localScreenStream.getAudioTracks().forEach((track) => {
                    track.stop();
                })
            } else {
                //TURN IT ON
                this.getStreams();
            }
        },
        toggleScreenShare() {
            if (!this.toggleScreenShare) {
                this.localScreenStream.getVideoTracks().forEach((track) => {
                    track.stop();
                });
            } else {
                this.getStreams();
            }
        }
    },
    created() {
        this.loadMyPeers();
        this.$socket.emit('initializeConnection', {
            sender: this.$store.getters.getUser.id
        })
    },

    methods: {
			closeAllStreams(){
			this.localScreenStream.getTracks().forEach((track)=>{
			track.close();
			});
		},
        callInitialization() {
            //CREATE PEER CONNECTION OBJECT
            this.peer = new RTCPeerConnection(this.networkConfig)
            //LISTEN FOR CANDIDATE
            this.peer.addEventListener('icecandidate', (event) => {
                if (event.candidate) {
                    console.log('candidate AVAILABLE' + event.candidate)
                    this.$socket.emit('candidate', {
                        candidate: event.candidate,
                        receiver: this.$store.getters.getChatClient.id
                    })
                } else {
                    console.log('NO candidate yet!')
                }
            })
            //LISTEN FOR THE REMOTE PEER MEDIA TRACK
            this.peer.addEventListener('track', async (event) => {
                this.remoteScreenStream.addTrack(event.track, this.remoteScreenStream);
                document.getElementById('localDisplay').srcObject = this.remoteScreenStream;
            })
        },
        getStreams() {
            navigator.mediaDevices.getDisplayMedia(this.constraint).then(this.handleStream).catch((e) => {
                console.log(e)
            })
        },
        handleStream(stream) {
            this.localScreenStream = stream;
            document.getElementById('localDisplay').srcObject = stream;
            stream.getTracks().forEach((track)=> {
                    this.peer.addTrack(track,stream)
                });
        },
        connectWithOthers() {},
        settings() {

        },
        checkIfYourPeersAreOnline() {
            let ids = [];
            let peers = this.$store.getters.getMyCodePeers;
            if (peers) {
                peers.forEach(function (element, index) {
                    ids[index] = element.id;
                });
                this.$socket.emit('checkConnectionResponseMultipleIds', {
                    clients: ids,
                    sender: this.$store.getters.getUser.id
                });
            }
        },
        signOut() {
            this.axios({
                    url: this.$store.getters.getApi + 'logout',
                    method: 'GET',
                    headers: this.connectionHeaders
                })
                .then((response) => {
                    this.searchError = '';
                    if (!response.data.error) {
                        this.$socket.emit('signout', {
                            client: this.$store.getters.getUser.id
                        })
                        this.$store.commit('setApiToken', undefined);
                        this.$store.commit('setUser', {});
                        this.$store.commit('setChatClient',{})
                        this.closeAllStreams();
                        this.$router.push('/welcome');
                    }
                })
                .catch((error) => {
                    if (error) {
                        this.alertType = 'error';
                        this.alertMessage = "Network Error";
                        this.alertShow = true;
                    }
                })
        },
        startSearchForPeer() {
            this.search(1);
        },
        //ADD A NEW PEER TO THE
        addPeer(index) {
            let peer = this.searchResults[index];
            this.progress = true;
            this.axios({
                    url: this.$store.getters.getApi + 'addPeer?user1=' + this.$store.getters.getUser.id + '&user2=' + peer.id,
                    method: 'GET',
                    headers: this.connectionHeaders
                })
                .then((d) => {
                    if (!d.data.error) {
                        this.progress = false;
                        let coders = this.$store.getters.getMyCodePeers;
                        coders.push({
                            id: peer.id,
                            username: peer.username,
                            profilePicture: peer.profile_picture,
                            status: false
                        });
                        this.$store.commit('setMyCodePeers', coders)
                    }
                })
                .catch((e) => {
                    if (e) {
                        this.progress = false;
                        this.searchError = 'Connection Error';
                    }
                })

        },
        search(page) {
            this.menu = false; //hide the menu
            this.searchResult = true;
            this.axios({
                    url: this.$store.getters.getApi + 'searchPeer?page=' + page + ' &search_query=' + this.searchPeers + '&user1=' + this.$store.getters.getUser.id,
                    method: 'GET',
                    headers: this.connectionHeaders

                })
                .then((d) => {
                    let data = d.data;
                    if (data.error) {
                        this.searchError = data.message;
                    } else {
                        this.searchResults = data.results;
                        this.totalSearchPages = data.totalPages;
                    }
                })
                .catch((er) => {
                    if (er) {
                        console.log(er)
                        this.searchError = "Network Error";
                    }
                })
        },
        setChatClient(client) {
			console.log(client)
            this.$store.commit('setChatClient', client);
            this.call();
            //alert(client.username)
        },
        loadMyPeers() {
            this.axios({
                    url: this.$store.getters.getApi + 'loadMyPeer?user1=' + this.$store.getters.getUser.id,
                    method: 'GET',
                    headers: this.connectionHeaders
                })
                .then((d) => {
                    let data = d.data;
                    this.$store.commit('setMyCodePeers', data.peers)
                })
                .catch((e) => {
                    if (e) {
                        return e;
                    }
                })
        },
        //WEBRTC CALL INITIATION FUNCTIONS
        sendAnswer(receiver) {
			alert('ANSWER CREATED')
            this.peer.createAnswer().then((answer) => {
                alert('ANSWER CREATED')
                this.peer.setLocalDescription(new RTCSessionDescription(answer))
                this.$socket.emit('answer', {
                    answer: answer,
                    receiver: receiver
                })
                console.log('ANSWER SENT')
            }).catch((error) => {
                if (error) {
                    alert('ERROR CREATING ANSWER ' + error)
                }
            })
        },
        //MEDIA
        retry() {
            this.call();
            this.onAC = false;
            this.notON = false;
            this.ON = false
            this.loader = true;
        },
        call() {
            let sender;
            sender = {
                id: this.$store.getters.getUser.id,
                profile_image: this.$store.getters.getUser.profile_picture,
                usn: this.$store.getters.getUser.username
            };

            this.$socket.emit("initiateVideo", {
                receiver: this.$store.getters.getChatClient.id,
                from: sender
            });
            window.setTimeout(() => {
                if (!this.receiverStatus) {
                    this.loader = false;
                    this.notON = true;
                    this.ON = false;
                    this.onAC = false;
                }
            }, 60000 * 2)
        },
        rejectVideoCall() {
            this.videoCallOverlay = false;
            this.$socket.emit('callReject', {
                receiver: this.caller
            })
        },
        acceptVideoCall() {
            this.videoCallOverlay = false;
            this.$socket.emit('callAccept', {
                receiver: this.caller
            });
            var ccl={
                id:this.caller.id , 
                username:this.caller.usn , 
                profilePicture:this.caller.profile_image , 
                status:true
            }
            console.log(this.caller)
            //SET THE CALLER AS YOUR CURRENT CHAT CLIENT
            this.$store.commit('setChatClient',ccl);
        },
    },
    unmounted(){
	this.closeAllStreams();
    },
    sockets: {
        initiateVideoRecv: function (data) {
            if (this.$store.getters.getUser.id === parseInt(data.data.receiver)) {
                this.caller = data.data.from;
                this.videoCallOverlay = true;
                this.callerSocketId = data.socketId;
            }
        },
        checkConnectionResponseMultipleIds: function (data) {
            if (data.receiver === this.$store.getters.getUser.id) {
                return;
            }
        },
        aNewConnection: function (data) {
            if (data.connectedUser !== this.$store.getters.getUser.id) {
                let peers = this.$store.getters.getMyCodePeers; //GET ALL MY CODE PEERS
                let count = 0;
                while (count < peers.length) {
                    if (peers[count].id === data.connectedUser) //SEARCH FOR THE CURRENT LOGGED IN PEER
                    {
                        peers[count].status = true; //update his/her status
                        break;
                    }
                    count += 1;
                }
                //UPDATE ALL THE CODE PEERS
                this.$store.commit('setMyCodePeers', peers);
            }
        },
        aDisconnection: function (data) {
            if (data.disconnectedUser !== this.$store.getters.getUser.id) {
                let peers = this.$store.getters.getMyCodePeers; //GET ALL MY CODE PEERS
                let count = 0;
                while (count < peers.length) {
                    if (peers[count].id === data.disconnectedUser) //SEARCH FOR THE CURRENT LOGGED IN PEER
                    {
                        peers[count].status = undefined; //update his/her status
                        break;
                    }
                    count += 1;
                }
                //UPDATE ALL THE CODE PEERS
                this.$store.commit('setMyCodePeers', peers);
            }
        },
        callReject: function () {
            this.loader = false;
            this.notON = false;
            this.ON = false;
            this.onAC = true;
            alert('call rejected by callee')
        },
        callAccept: function () {
            this.loader = false;
            this.notON = false;
            this.ON = true;
            this.onAC = false;
            this.remotePeerId = this.$store.getters.getChatClient.id;
            //SEND AN OFFER TO THE REMOTE PEER
            this.peer.createOffer().then((offer) => {
                console.log('offer created')
                //SET LOCAL DESCRIPTION
                this.peer.setLocalDescription(new RTCSessionDescription(offer))
                //SEND TO THE REMOTE PEER
                this.$socket.emit('offer', {
                    offer: offer,
                    receiver: this.remotePeerId
                })
            }).catch((error) => {
                if (error) {
                    console.log('Error creating offer')
                }
            })
        },
        candidate: function (data) {
            //THAT MEANS A CANDIDATE IS READY
            this.peer.addIceCandidate(data.candidate)
            console.log('candidate added!')
        },
        offer: function (data) {
            //ACCEPT AN OFFER
            console.log('RECEIVED AN OFFER')
            this.peer.setRemoteDescription(new RTCSessionDescription(data.offer))
            this.loader = false;
            this.notON = false;
            this.ON = true;
            this.onAC = false;
            //CREATE AN ANSWER
            this.sendAnswer(data.sender);
        },
        answer: function (data) {
            //ACCEPT THE ANSWER
            this.peer.setRemoteDescription(new RTCSessionDescription(data.answer))
        }
    },
    mounted() {
        let width = window.innerWidth;
        let height = window.innerHeight;
        this.displayHeight = height * .85;
        this.displayWidth = width * .7;
        this.checkIfYourPeersAreOnline()
        this.getStreams();
        this.callInitialization();
    },
    components: {
        Message
    }
}
</script>

