<template>
<div>
    <div id="slideCont" v-if="$store.getters.getChatClient">
        <div id="slider">
            <div v-for="(message,index) in $store.getters.getLiveMessages" :key="index">
                <v-card color="#283593" dark class="me" v-if="message.sender===$store.getters.getMyId">
                    <v-card-text>{{message.content}}</v-card-text>
                    <v-card-actions>
                        <v-btn @click.stop="deleteMessage(index)" icon><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 48 48">
                                <path fill="#fff" d="M38 12.83L35.17 10 24 21.17 12.83 10 10 12.83 21.17 24 10 35.17 12.83 38 24 26.83 35.17 38 38 35.17 26.83 24z" /></svg></v-btn>
                        <v-spacer />
                        <v-chip color="rgba(0,0,0,.2)" class="float-right" small>{{message.timeSent}}</v-chip>
                    </v-card-actions>
                </v-card>

                <v-card color="#4527a0" dark rounded class="peer" v-if="message.sender!==$store.getters.getMyId">
                    <v-card-text>{{message.content}}</v-card-text>
                    <v-card-actions>
                        <v-btn @click.stop="deleteMessage(index)" icon><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 48 48">
                                <path fill="#fff" d="M38 12.83L35.17 10 24 21.17 12.83 10 10 12.83 21.17 24 10 35.17 12.83 38 24 26.83 35.17 38 38 35.17 26.83 24z" /></svg></v-btn>
                        <v-spacer />
                        <v-chip color="rgba(0,0,0,.2)" small>{{message.timeSent}}</v-chip>
                    </v-card-actions>
                </v-card>
            </div>
        </div>
    </div>
    <div>
        <v-textarea fixed bottom right outlined class="mx-2" label="Message" v-model="message" placeholder="Message Content">
            <template v-slot:append>
                <v-btn fab small color="#3f51b5" dark @click.stop="sendMessage">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 48 48">
                        <path fill="#fff" d="M4.02 42L46 24 4.02 6 4 20l30 4-30 4z" /></svg>
                </v-btn>
            </template>
        </v-textarea>
    </div>
</div>
</template>

<script>
export default {
    name: 'Message',
    data() {
        return {
            message:'',
            connectionHeaders: {
                'Access-Control-Allow-Origin': '*',
                'Authorization': 'Bearer ' + this.$store.getters.getApiToken
            },
        }
    },
    created(){
        if(!this.$store.getters.getMyId){
            this.$router.push('/welcome');
        }
    },
    methods: {
        sendMessage(){
            //CHECK IF THERE ARE ANY PEARED PEER
            if(this.$store.getters.getChatClient.id){
               //UPDATE THE LIVE MESSAGES
               var messages = this.$store.getters.getLiveMessages;
               //APPEND THE NEW MESSAGE
               var msg={
                id:undefined,
                trackingId:new Date().getTime()+'_MSG',
                timeSent:new Date().getTime(),
                content:this.message,
                sender:this.$store.getters.getMyId,
                receiver:this.$store.getters.getChatClient.id,
                status:false
               }
               messages.push(msg);
               var d=new FormData();
               d.append('trackingId',msg.trackingId);
               d.append('content',msg.content);
               d.append('sender',msg.sender);
               d.append('receiver',msg.receiver); 
               //SEND THE MESSAGE TO DATABASE
               this.axios({
                    url: this.$store.getters.getApi + 'sendMessage',
                    method: 'POST',
                    data:d,
                    headers: this.connectionHeaders
               }).then((data)=>{
                var d=data.data;
                if(d.status){
                    this.$socket.emit('sendMessage',msg);
                    this.message=null;
                }
               }).catch((error)=>{
                alert(error)
                var messages = this.$store.getters.getLiveMessages
                messages.splice(messages.length-1,1);
               })
            }
        }
    },
    sockets: {
        messageNotReceived:function(data){
            console.log(data);
        }
    }
}
</script>

<style>
#slideCont {
    max-height: 320px;
    overflow: scroll;
    padding: 2px;
}

.peer {
    margin-right: 25px;
    margin-top: 4px;
}

.me {
    margin-left: 25px;
    margin-top: 4px;
    border-radius: 25px;
}
</style>
